	import {uniCollapse,uniCollapseItem} from "@dcloudio/uni-ui"
	export default {
		data() {
			const currentDate = this.getDate({
            format: true
			})
			return {
				array: ['20以下', '50', '100', '100以上'],
                index: "",
				selectValue:"", 
				imageList:[],
				room_address:"广州市越秀区东风东路丽丰中心",
				room_linkman:"陈先生",
				room_tel:"15696565658",
				date: currentDate,
				time: '12:01',
				
				labelName:'',
				labelDataList:'',
				 labelList: [{
				    name: '投影仪',
				    value: '0',
				    checked: false
				  },
				    {
				      name: '幕布',
				      value: '1',
				      checked: false
				    },
				    {
				      name: '电脑',
				      value: '2',
				      checked: false
				    },
				    {
				      name: '麦克风',
				      value: '3',
				      checked: false
				    },
				    {
				      name: '音响',
				      value: '4',
				      checked: false
				    },{
				    name: '白板',
				    value: '0',
				    checked: false
				  },
				    {
				      name: '灯光',
				      value: '1',
				      checked: false
				    },
				    {
				      name: '摄像机',
				      value: '2',
				      checked: false
				    }
				],
					
				
				
			}
		},
		methods: {
			bindPickerChange: function(e) {
            console.log('picker发送选择改变，携带值为', e.target.value)
            this.index = e.target.value
			},
			bindDateChange: function(e) {
            this.date = e.target.value
			},
			bindTimeChange: function(e) {
            this.time = e.target.value
			},
			previewImage: function(e) {
				var current = e.target.dataset.src
				uni.previewImage({
					current: current,
					urls: this.imageList
				})
			},
			  labelBtn:function(name,index){
			  console.log(name,index,"nameindex")
			  this.labelName = name
			  if(this.labelDataList.join(',').indexOf(name) >-1){
			    this.labelList[index].checked = true
			  }else{
			    this.labelList[index].checked = false
			  }
			},
			  checkboxChange: function (e) {
			  this.labelDataList = e.detail.value
			  console.log(e)
			  console.log('checkbox发生change事件，携带value值为：' + e.detail.value)
			  console.log(this.labelDataList,"labelDataList")
			},
			 getDate:function(type) {
            const date = new Date();
            let year = date.getFullYear();
            let month = date.getMonth() + 1;
            let day = date.getDate();

            if (type === 'start') {
                year = year - 60;
            } else if (type === 'end') {
                year = year + 2;
            }
            month = month > 9 ? month : '0' + month;;
            day = day > 9 ? day : '0' + day;
            return `${year}-${month}-${day}`;
           },
			chooseImage: async function() {
					if (this.imageList.length === 9) {
						let isContinue = await this.isFullImg();
						console.log("是否继续?", isContinue);
						if (!isContinue) {
							return;
						}
					}
					uni.chooseImage({
						success: (res) => {
							this.imageList = this.imageList.concat(res.tempFilePaths);
						}
					})
				},
				isFullImg: function() {
					return new Promise((res) => {
						uni.showModal({
							content: "已经有9张图片了,是否清空现有图片？",
							success: (e) => {
								if (e.confirm) {
									this.imageList = [];
									res(true);
								} else {
									res(false)
								}
							},
							fail: () => {
								res(false)
							}
						})
					})
				},
		},
		 components: {uniCollapse,uniCollapseItem},
		 computed: {
        startDate() {
            return this.getDate('start');
        },
        endDate() {
            return this.getDate('end');
        }
    },
	}
